import { Component, OnInit } from '@angular/core';
import { NgbNavModule } from '@ng-bootstrap/ng-bootstrap';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { ActivatedRoute, Router } from '@angular/router';
import { ConfigService } from 'src/app/service/config.service';
import { NgZone } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

declare let navigator: any;
declare let Camera: any;

export class Model {
  constructor(
    public x_activity_type_id: string,
    public x_note: string,
    public x_summary: string,
    public x_visit_reason: string,
    public x_not_visit_reason: string,

  ) { }

}

@Component({
  selector: 'app-activity-detail',
  templateUrl: './activity-detail.component.html',
  styleUrls: ['./activity-detail.component.css']
})
export class ActivityDetailComponent implements OnInit {
  loading: boolean = false;
  api: string = environment.api;
  note: string = "";
  model: any = new Model("", "", "", "", "");
  id: string = "";
  item: any = [];
  active = 1;
  geoData: any = [];
  constructor(
    private http: HttpClient,
    private router: Router,
    private activeRoute: ActivatedRoute,
    private configService: ConfigService,
    private ngZone: NgZone,
    private modalService: NgbModal
  ) { }

  ngOnInit(): void {
    this.id = this.activeRoute.snapshot.params['id'];
    this.httpGet();
  }

  httpGet() {
    this.modalService.dismissAll();
    this.loading = true;
    this.http.get<any>(this.api + 'activities/detail/' + this.id, {
      headers: this.configService.headers(),
    }).subscribe(
      data => {
        this.loading = false;
        console.log(data);
        this.item = data['item'];
        this.model.x_activity_type_id = data['item']['x_activity_type_id'];
        this.model.x_note = data['item']['x_note'];
        this.model.x_summary = data['item']['x_summary'];
      },
      e => {
        console.log(e);
        this.note = "Error Server!";
      },
    );
  }

  remove() {
    const body = {
      id: this.id,
      model: this.model
    }
    if (confirm("is not visit this activity?")) {
      this.http.post<any>(this.api + 'activities/remove', body, {
        headers: this.configService.headers(),
      }).subscribe(
        data => {
          this.modalService.dismissAll();
          console.log(data);
          history.back();
        },
        e => {
          console.log(e);
          this.note = "Error Server!";
        },
      );
    }
  }

  checkIn() {
    const body = {
      id: this.id,
      model: this.model,
      geoData: this.geoData,
    }
    console.log(body);
    this.http.post<any>(this.api + 'activities/checkIn', body, {
      headers: this.configService.headers(),
    }).subscribe(
      data => {
        console.log(data);
        if (environment.cam === true) {
          this.takePhoto();
        }

        this.httpGet();
      },
      e => {
        console.log(e);
        this.note = "Error Server!";
      },
    );
  }

  checkOut() {
    const body = {
      id: this.id,
      model: this.model,
      geoData: this.geoData
    }
    console.log(body);
    this.http.post<any>(this.api + 'activities/checkOut', body, {
      headers: this.configService.headers(),
    }).subscribe(
      data => {
        console.log(data);
        if (environment.cam === true) {
          this.takePhoto();
        }
        this.httpGet();
      },
      e => {
        console.log(e);
        this.note = "Error Server!";
      },
    );
  }

  getGeo() {
    if (environment.cam == true) { 
      let self = this;
      var onSuccess = function (position: any) {
        console.log('Latitude: ' + position.coords.latitude + '\n' +
          'Longitude: ' + position.coords.longitude + '\n' +
          'Altitude: ' + position.coords.altitude + '\n');
        self.geoData = {
          lat: position.coords.latitude,
          long: position.coords.longitude,
          timestamp: position.timestamp,
        };
        if (self.item.x_activity_status == 'OPEN') {
          self.checkIn();
        } else {
          self.checkOut();
        }

      };

      function onError(error: any) {
        console.log('code: ' + error.code + '\n' + 'message: ' + error.message + '\n');
        //self.checkIn();
        alert("GPS ERROR, PLEASE SET PERMISSION!");
      }
      navigator.geolocation.getCurrentPosition(onSuccess, onError);
    }else{
      this.geoData = {
        lat: 0,
        long: 0,
        timestamp:0,
      };
      if (this.item.x_activity_status == 'OPEN') {
        this.checkIn();
      } else {
        this.checkOut();
      }
    }
  }

  back() {
    history.back();
  }


  images: string = "";
  initPhoto: boolean = false;
  takePhoto() {
    const options = {
      destinationType: Camera.DestinationType.DATA_URL,
      quality: 50,
      encodingType: Camera.EncodingType.JPEG,
      correctOrientation: true,
    };
    let self = this;

    navigator.camera.getPicture(this.cameraSuccess, this.cameraError, options);

  }
  cameraSuccess = (imagesData: any) => {
    this.initPhoto = true;
    this.loading = false;
    this.ngZone.run(() => {
      this.images = 'data:image/png;base64,' + imagesData;
      this.saveImages()
    });

  }

  cameraError = (e: any) => {
    console.log('error Camera', e);
    this.initPhoto = false;
    this.modalService.dismissAll();
  }

  saveImages() {
    // this.initPhoto = true;
    if (this.initPhoto == true) {
      this.loading = true;
      const body = {
        id: this.id,
        base64Images: this.images,
        status: this.item.x_activity_status
        //base64Images : "data:image/png;base64,"
      }
      console.log(body);
      this.http.post<any>(this.api + 'activities/takePhoto', body, {
        headers: this.configService.headers(),
      }).subscribe(
        data => {
          console.log(data);
          this.loading = false;
          this.modalService.dismissAll();
          this.httpGet();
        },
        e => {
          console.log(e);
          this.note = "Error Server!";
        },
      );
    }
  }


  openFullscreen(content: any) {
    this.modalService.open(content, { fullscreen: true });
  }


}
